<script lang="ts">
export default {
  inheritAttrs: true,
}
</script>

<script setup lang="ts">
import { DefaultColor, IMaterialItem, IModuleConfigGroup, PageNode, DataStatus } from '@/config'
import { usePageStore } from '@/stores/page'
import { storeToRefs } from 'pinia'
import InputItem from '../configs/items/InputItem.vue'
import Btn from '../widgets/Btn.vue'
import SwitchItem from '../configs/items/SwitchItem.vue'
import Modal from './Modal.vue'
import { createMaterialSnapshot } from '@/utils/snapshot'
import { watch, nextTick } from 'vue'
import TreeNode from '../biz/TreeNode.vue'
import ImageItem from '../configs/items/ImageItem.vue'
import { Alert, AlertError } from '@/utils/alert'
import { useUserStore } from '@/stores/user'
import { cloneDeep } from 'lodash-es'
import { uploadByBase64 } from '@/utils/oss'
import { createJSONEditor } from '@/utils/jsoneditor'
import { $t } from '@/constants/i18n'
import type JSONEditor from 'jsoneditor'
import Switch from '../widgets/Switch.vue'
import { copyToClipboard } from '@/utils/clipboard'

interface SaveMaterialModalProps {
  modelValue: boolean
  material?: IMaterialItem
  autoCreateCover?: boolean
  actionText?: string
  hideCreateCover?: boolean
  onSave?: ((material: IMaterialItem) => void) | null
}

const { modelValue, material, autoCreateCover, actionText = $t('save'), hideCreateCover, onSave } = defineProps<SaveMaterialModalProps>()

const pageStore = usePageStore()
const { colorVars } = storeToRefs(pageStore)
const { fetchSaveMaterial } = pageStore

const userStore = useUserStore()
const { isAdmin } = storeToRefs(userStore)

let editItem: IMaterialItem | null = $ref(null)
let coverLoading = $ref(!!autoCreateCover)

let moduleConfigEditor = $ref<JSONEditor | null>(null)
let moduleDependenceElemEditor = $ref<JSONEditor | null>(null)
let modal = $ref<InstanceType<typeof Modal> | null>(null)

const isTemplate = $computed<boolean>(() => material?.type === 'template')
const node = $computed<PageNode | null>(() => (isTemplate ? null : (editItem?.node as PageNode)))
const isModule = $computed<boolean>({
  get() {
    return (!isTemplate && node?.isModule) || false
  },
  set(val: boolean) {
    if (!editItem?.node) return
    if (val) nextTick(() => initJSONEditor())
    editItem.node.isModule = val
  },
})

/** 初始化 JSON 编辑器 */
const initJSONEditor = async () => {
  if (!editItem || !node || !isAdmin) return
  moduleConfigEditor = await createJSONEditor('.module-config')
  if (!moduleConfigEditor) return
  moduleConfigEditor.set(
    node.moduleConfig && node.moduleConfig?.length > 0 ? node.moduleConfig :
      ([
        {
          title: '标题',
          titleEn: 'Title',
          icon: 'basic',
          defaultCollapsed: true,
          data: [
            {
              type: 'text',
              label: '文本',
              labelEn: 'Text',
              props: { type: 'textarea' },
              sourceValue: 'children[0].config.props.basic.text',
              targetValue: 'children[0].config.props.basic.text',
            },
          ],
        },
      ] as IModuleConfigGroup[])
  )
}

const initJSONDependence = async () => {
  if (!editItem || !node || !isAdmin) return
  moduleDependenceElemEditor = await createJSONEditor('.module-dependence')
  if (!moduleDependenceElemEditor) return
  moduleDependenceElemEditor.set(
    node.moduleDependence || {
      customFontFace: '',
      colorVars: colorVars.value,
    } as PageNode['moduleDependence']
  )
}

/** 自动生成封面 */
const handleCreateCover = async () => {
  if (!node || !editItem) return
  try {
    coverLoading = true
    const elem = document.querySelector(`[data-name="${node.name}"]`) as HTMLElement
    if (!elem) return
    const cover = await createMaterialSnapshot(elem)
    if (cover.length >= 10000) {
      const url = await uploadByBase64(cover)
      editItem.cover = url
    } else {
      editItem.cover = cover
    }
  } finally {
    coverLoading = false
  }
}

watch(
  () => [material, modelValue],
  async () => {
    if (material && modelValue) {
      editItem = cloneDeep(material)
      nextTick(() => {
        initJSONEditor()
        initJSONDependence()
      })
      if (autoCreateCover) {
        await handleCreateCover()
      }
    } else {
      editItem = null
      moduleConfigEditor?.destroy()
      moduleDependenceElemEditor?.destroy()
    }
  },
  { immediate: true }
)

const handleSave = async () => {
  if (!editItem?.name) {
    AlertError($t('nameRequired'))
    return
  }
  const data = await fetchSaveMaterial({
    ...editItem,
    node: isTemplate
      ? void 0
      : ({
          ...node,
          isModule: node?.isModule || false,
          moduleConfig: node?.isModule && moduleConfigEditor ? moduleConfigEditor.get() : [],
          moduleDependence: moduleDependenceElemEditor ? moduleDependenceElemEditor.get() : null,
        } as PageNode),
  })
  onSave?.(data)
  Alert($t('saveSuccess'))
  modal?.hide()
}

/** 点击 TreeNode 时候自动把 children 路径贴到粘贴板 */
const handleTreeNodeClick = (e: Event) => {
  let targetElem = e.target as HTMLDivElement
  let list = []
  while (targetElem && !targetElem.classList.contains('node-tree')) {
    if (targetElem.classList.contains('tree-node')) {
      list.unshift(
        Array.from((targetElem.parentElement as HTMLDivElement).children).indexOf(targetElem)
      )
    }
    targetElem = targetElem.parentElement as HTMLDivElement
  }
  list.shift()
  setClipboard(list.map(i => `children[${i}]`).join('.'))
}

/** 设置粘贴板 */
const setClipboard = async (text: string) => {
  await copyToClipboard(text)
  Alert('copy to clipboard: ' + text)
}

/** 状态变更 */
const handleStatusChange = (bool: boolean) => {
  if (!editItem) return
  editItem.status = bool ? DataStatus.Normal : DataStatus.Hidden
}

const titleMap = {
  template: $t('template'),
  component: $t('component'),
  section: $t('section'),
}

const setValue = (key: 'name' | 'enName' | 'category' | 'categoryEn' | 'cover', value: string) => {
  if (!editItem) return
  editItem[key] = value
}
</script>

<template>
  <Modal
    ref="modal"
    class="save-modal"
    :title="`${actionText} ${titleMap[material!.type]}`"
    :width="'70vw'"
    close-on-click-mask
    :model-value="modelValue"
    v-bind="$attrs"
  >
    <div class="info-wrapper" v-if="editItem">
      <div class="info">
        <InputItem :label="$t('name')" v-model="editItem.name"></InputItem>
        <InputItem
          v-if="isAdmin"
          :label="$t('nameEn')"
          :model-value="editItem.enName || ''"
          @update:model-value="(val: string) => setValue('enName', val)"
        ></InputItem>
        <InputItem
          :label="$t('category')"
          v-if="!isTemplate"
          :model-value="editItem.category || ''"
          @update:model-value="(val: string) => setValue('category', val)"
        ></InputItem>
        <InputItem
          v-if="isAdmin && !isTemplate"
          :label="$t('categoryEn')"
          :model-value="editItem.categoryEn || ''"
          @update:model-value="(val: string) => setValue('categoryEn', val)"
        ></InputItem>
        <SwitchItem v-if="isAdmin && !isTemplate" :label="$t('moduleSwitch')" v-model="isModule"></SwitchItem>
      </div>
      <ImageItem
        hide-label
        :loading="coverLoading"
        wrapper-class="image-item"
        :rows="5"
        :model-value="editItem.cover || ''"
        @update:model-value="(val: string) => setValue('cover', val)"
      ></ImageItem>
    </div>
    <div class="module-setting-wrapper" v-if="isAdmin && !isTemplate" @keydown.stop>
      <div class="module-config-wrapper" v-if="isModule">
        <div class="module-input module-config"></div>
        <div class="node-tree" v-if="node" @click="handleTreeNodeClick">
          <TreeNode :node="node" :preview="true"></TreeNode>
        </div>
      </div>
      <div class="module-dependence-wrapper">
        <div class="module-input module-dependence"></div>
      </div>
    </div>
    <div class="btn-wrapper">
      <div class="btn-wrapper-left">
        <Btn
          v-if="!hideCreateCover"
          class="create-cover-btn"
          type="text"
          size="sm"
          @click="handleCreateCover"
          >{{ $t('autoCreateCover') }}</Btn
        >
      </div>
      <Switch
        v-if="isAdmin && editItem"
        :model-value="!editItem.status || editItem.status === DataStatus.Normal"
        @update:model-value="handleStatusChange"
      ></Switch>
      <Btn class="save-btn" type="inner" :text="$t('save')" @click="handleSave"></Btn>
    </div>
  </Modal>
</template>

