      margin-left: 24px;

      .input {
        height: 100%;
      }
    }
  }
  .item {
    display: flex;
    align-items: center;

    &:not(:last-child) {
      margin-bottom: 12px;
    }
    .label {
      flex: 1;
      margin-right: 10px;
      font-size: 14px;
    }
    .input-wrapper {
      width: 180px;
      padding: 0;

      input {
        padding: 0 8px;
      }
    }
  }

  .module-setting-wrapper {
    display: flex;
    flex-direction: column;
    height: 300px;
    padding: 8px;
    overflow: hidden;
    border-radius: $normal-radius;
    border: 1px dashed rgba($color, 60%);
    margin-bottom: 10px;

    .module-config-wrapper {
      flex: 1;
      display: flex;
      overflow: hidden;
    }

    .module-dependence-wrapper {
      height: 80px;
      flex-shrink: 0;
      overflow: hidden;

      &:only-child {
        height: 100%;
      }
    }

    .module-input {
      flex: 1;
      height: 100%;
      overflow: hidden;
      border-radius: $inner-radius 0 0 $inner-radius;
