/** 数据列表 */
export enum DataType {
  Page = 'page',
  Resume = 'resume',
}

/** 组件对应支持的配置分组 */
export const ComponentPropsGroup = {
  Block: [
    'common',
    'layout',
    'size',
    'spacing',
    'border',
    'background',
    'container',
    'position',
    'event',
    'effect',
    'animation',
  ] as const,
  Text: [
    'common',
    'basic',
    'font',
    'spacing',
    'border',
    'background',
    'container',
    'position',
    'event',
    'effect',
    'animation',
  ] as const,
  Image: [
    'common',
    'basic',
    'size',
    'spacing',
    'border',
    'container',
    'position',
    'event',
    'effect',
    'animation',
  ] as const,
  Icon: ['common', 'basic', 'spacing', 'border', 'event', 'effect'] as const,
} as const

/** 支持的配置分组名 */
export type GroupType =
  | 'common'
  | 'basic'
  | 'size'
  | 'layout'
  | 'font'
  | 'spacing'
  | 'border'
  | 'background'
  | 'container'
  | 'position'
  | 'event'
  | 'effect'
  | 'animation'
  | 'custom'

/** 配置对应约束类型 */
export interface GroupPropType<T extends ComponentName = any> {
  common: ICommonType
  basic: {
    Text: ITextBasicType
    Image: IImageBasicType
    Icon: IIconBasicType
    [key: string]: any
  }[T]
  size: ISize
  layout: ILayout
  font: IFont
  spacing: ISpacing
  border: IBorder
  background: IBackground
  container: IContainer
  position: IPosition
  event: IEvent
  effect: IEffect
  animation: IAnimation
  custom: any
}

export type ComponentPropsGroupType = typeof ComponentPropsGroup
export type ComponentName = keyof ComponentPropsGroupType

type IsAny<T> = 0 extends 1 & T ? true : false

/** 将配置分组名和约束类型相对应 */
export type PropsTypes<T extends ComponentName = any> = IsAny<T> extends false
  ? Pick<GroupPropType<T>, ComponentPropsGroupType[T][number]>
  : Partial<{
      [key in GroupType]: GroupPropType[key]
    }> &
      Pick<GroupPropType<'Block'>, 'common' | 'effect' | 'event' | 'border' | 'spacing'>

export interface IPropConfig<T extends ComponentName = any> {
  /** 配置参数 */
  props: PropsTypes<T>
  /** 当客户端支持 Desktop & Mobile 时，可以单独配置 mobile 配置 */
  mobile?: Partial<PropsTypes<T>>
}

/** 一份页面的完整数据 */
export interface IPage {
  /** 页面数据 */
  pageData: PageNode[]
  /** 颜色变量 */
  colorVars: IColorVarItem[]
  /** 页面配置 */
  setting: IPageSetting
  /** 字体配置 */
  font: IFontSetting
}

export type PageNodeType = 'component' | 'section' | 'template'

/** 单个可配置的组件 */
export interface PageNode<T extends ComponentName = any> {
  /**
   * 组件类型
   * component: 最小粒度组件，无法添加 children
   * section: 块组件，可以添加 children
   * template: 模板组件
   */
  type: PageNodeType
  /** 组件名称 */
  name: string
  /** 标签 */
  tags: string[]
  /** 组件名称 */
  component: T
  /** 参数 */
  props?: PropsTypes<T>
  /** 组件配置 */
  config: IPropConfig<T>
  /** 配置参数链接到其他组件 */
  propLink?: string
  /** 包含的子组件 */
  children?: PageNode[]
  /** 组件封面图，在物料栏里展示 */
  cover?: string
  /** 是否已封装成模块 */
  isModule?: boolean
  /** 模块设置 */
  moduleConfig?: IModuleConfigGroup[]
  /** 模块依赖项 */
  moduleDependence?: {
    /** 字体依赖 */
    customFontFace?: IFontSetting['customFontFace'][0]
    /** 颜色变量依赖 */
    colorVars?: IColorVarItem[]
  }
  /** 物料来源 */
  materialId?: string
}

/** 模块配置支持类型 */
export enum ModuleConfigType {
  /** 按钮 */
  // Btn = 'btn',
  /** 文本 */
  Text = 'text',
  /** 颜色 */
  Color = 'color',
  /** 字体大小 */
  FontSize = 'fontSize',
  /** 透明度 */
  Opacity = 'opacity',
  /** 图片 */
  Image = 'image',
  /** 背景图片尺寸 */
  BackgroundSize = 'backgroundSize',
  /** 提示 */
  Tip = 'tip',
  /** 尺寸 */
  Size = 'size',
  /** 一组数据 */
  // Group = 'group',
}
export interface IModuleConfigGroup {
  /** 配置名称 */
  title: string
  /** 配置名称（英文） */
  titleEn?: string
  /** 分组数据 */
  data: IModuleConfigItem[]
  /** 图标 */
  icon?: string
  /** 是否默认展开 */
  defaultCollapsed?: boolean
}
/** 模块单项配置 */
export interface IModuleConfigItem {
  /** 配置类型 */
  type: ModuleConfigType
  /** label */
  label?: string
  /** 配置名称（英文） */
  labelEn?: string
  /** 配置组件传参参数 */
  props?: any
  /** 获取值 */
  sourceValue?: string
  /** 设置值 */
  targetValue?: string | string[]
}

/** 数据状态 */
export enum DataStatus {
  /** 正常显示 */
  Normal = 1,
  /** 隐藏 */
  Hidden = 2,
}

/** 物料数据 */
export interface IMaterialItem {
  /** 物料名称 */
  name: string
  enName?: string
  /** 组件数据，type 为 component/section 时存在 */
  node?: PageNode
  /** 页面数据，type 为 template 时存在 */
  page?: IPage
  /** 数据类别，以使用场景区分 */
  dataType?: DataType
  /** 类型，有 component/section/template */
  type: PageNodeType
  /** 分组 */
  category?: string
  categoryEn?: string
  /** 缩略图 */
  cover?: string
  id?: string
  uid?: string
  /** 数据状态 */
  status?: DataStatus
  createDate?: Date
  updateDate?: Date
}

/** 判断是否某个基础类型 */
export const isSomeBasicType = <T extends ComponentName, P extends GroupPropType<T>['basic']>(
  name: string,
  compare: T,
  basic: any
): basic is P => {
  return name === compare && !!basic
}

export const DefaultIconStyleLink =
  'https://netdna.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css'

export const imgErrorFallback = 'data:image/png;base64,'

export const DefaultColor = '#333333'

export const getUniqueName = (name: string) => `_name_${name}_`
export const getTagClassName = (tag: string) => `_tag_${tag}_`
